import WriteOff from "@/components/WriteOff";
import {
	isWeixin
} from "@/utils";
// import { wechatEvevt } from "@/libs/wechat";
import {
	orderVerific
} from "@/api/order";
const NAME = "OrderCancellation";

export default {
	name: NAME,
	components: {
		WriteOff
	},
	props: {},
	data: function() {
		return {
			iShidden: true,
			iswechat: isWeixin(),
			orderInfo: {},
			verify_code: ""
		};
	},
	mounted: function() {},
	methods: {
		cancel: function(res) {
			this.iShidden = res;
		},
		confirm: function() {
			orderVerific(this.verify_code, 1)
				.then(res => {
					this.iShidden = true;
					this.verify_code = "";
					uni.showToast({
						title: res.msg,
						icon: 'none',
						duration: 2000
					});
				})
				.catch(err => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});

				});
		},
		storeCancellation: function() {
			let ref = /[0-9]{12}/;
			if (!this.verify_code) {
				uni.showToast({
					title: "请输入核销码",
					icon: 'none',
					duration: 2000
				});
				return
			}
			if (!ref.test(this.verify_code)) {
				uni.showToast({
					title: "请输入正确的核销码",
					icon: 'none',
					duration: 2000
				});
				return
			}
			uni.showLoading({
				title: "查询中"
			});
			orderVerific(this.verify_code, 0)
				.then(res => {
					uni.hideLoading();
					this.orderInfo = res.data;
					this.iShidden = false;
				})
				.catch(() => {
					uni.hideLoading();
				});
		},
		openQRCode: function() {
